<template>
  <div class="todolist-wrap">
    <half-circle-spinner v-if="!isLoading" :animation-duration="1000" :size="40" :color="'#007bff'" />
    <div v-else>
      <todo-add />
      <label for="dateline" class="col-form-label">
        <font-awesome-icon fixed-width class="ml-auto text-dark" size="lg" icon="folder-open" />
        {{group}}
      </label>

      <template v-for="(item, key) in todos">
        <TodoSingleRow :todo='item' :key="key" v-if="!item.isCheck && item.isShow" />
      </template>

      <div v-if="hasIsCheckTodoGroup" class="text-center">
        <button type="button" class="btn btn-outline-primary py-1 my-2" @click="isShowCheckedTodo = !isShowCheckedTodo">顯示已完成項目</button>
      </div>

      <div v-if="isShowCheckedTodo">
        <template v-for="(item, key) in todos">
          <TodoSingleRow :todo='item' :key="key" v-if="item.isCheck" />
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import TodoGroupRow from '@/components/template/todo-group-row.vue'
import TodoSingleRow from '@/components/template/todo-single-row.vue'
import TodoAdd from '@/components/template/todo-add'

import { rtdbDB, getUserDBRef } from '@/service/rtdb_db'

export default {
  props: ['group'],
  components: {
    TodoGroupRow,
    TodoSingleRow,
    TodoAdd
  },
  data () {
    return {
      isShowCheckedTodo: false,
      todos: {}
    }
  },
  computed: {
    hasIsCheckTodoGroup () {
      let todoList = this.todoTurnArray
      return todoList.filter(item => item.isCheck === true).length > 0
    },
    todoTurnArray () {
      return Object.keys(this.todos).map(item => {
        return this.todos[item]
      })
    },
    isPrevAddTodo () {
      return this.$store.state.isAddTodo
    }
  },
  methods: {
    changeIconsByImportant (isImportant) {
      if (isImportant) {
        return ['fas', 'star']
      }
      return ['far', 'star']
    },
    changeIconsByCheck (isCheck) {
      if (isCheck) {
        return ['fas', 'check-square']
      }
      return ['far', 'square']
    }
  },
  created () {
    // rtdbDB.ref(`${getUserDBRef()}/todoList`).child(`${this.group}`).orderByChild('isImportant').on('value', snapshot => {
    //   // let data = {}
    //   // let id = ''
    //   // snapshot.forEach(function (item, i) {
    //   //   id = item.key
    //   //   data[id] = item.val()
    //   // })

    //   let data = []
    //   snapshot.forEach(item => {
    //     let itemData = item.val()
    //     itemData.key = item.key
    //     data.push(itemData)
    //   })

    //   this.todos = data.reverse()
    //   this.isLoading = true
    // })

    if (this.isPrevAddTodo) this.$messageHandle('success', '新增成功')

    rtdbDB.ref(`${getUserDBRef()}/todoList`).child(`${this.group}`).orderByChild('isImportant').on('value', snapshot => {
      let data = []

      const init = () => new Promise((resolve, reject) => {
        if (snapshot) {
          resolve(snapshot)
        } else {
          reject(new Error('目前無資料'))
        }
      })

      init()
        .then(snapshot => {
          snapshot.forEach(item => {
            let itemData = item.val()
            itemData.key = item.key
            data.push(itemData)
          })

          this.todos = data.reverse()
        })
        .catch(err => {
          this.$swalErrorHandle(err.message)
        })
        .finally(() => {
          this.isLoading = true
        })
    })
  }
}
</script>



